import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { makeStyles } from '@material-ui/core/styles';

import Follow from '../../components/Following/Follow';
import Unfollow from '../../components/Following/Unfollow';
import { getFollowingCoinsDetails } from '../../store/actions/actionHome';

export interface Props {}

const useStyles = makeStyles((theme) => ({
  followingContainer: {
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    alignItems: 'center',
    height: '355px',
    width: '100%',
  },
}));

export const Following: React.FC<Props> = (props) => {
  const classes = useStyles();

  const dispatch = useDispatch();
  const followingCoins = useSelector((state: any) => state.home.followingCoins);
  const followingCoinsDetails = useSelector(
    (state: any) => state.home.followingCoinsDetails,
  );

  useEffect(() => {
    if (followingCoins.length > 0 && !followingCoinsDetails) {
      console.log(followingCoins);
      console.log(followingCoinsDetails);
      dispatch(getFollowingCoinsDetails(followingCoins));
    }
  }, [dispatch, followingCoins, followingCoinsDetails]);

  return (
    <div className={classes.followingContainer}>
      {followingCoins.length > 0 ? <Follow /> : <Unfollow />}
    </div>
  );
};

export default Following;
